  display: none;
  box-sizing: border-box;
  padding: 20px 50px 50px 50px;
  width: 960px;
  background-color: #FFFFFF;
  box-shadow: 0px 1px 6px 0px rgba(50, 50, 50, 0.6);
  transform: translateX(-50%); }
  .modal--show {
    display: block; }
  .modal label {
    color: #000000;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px; }
  .modal input {
    box-sizing: border-box;
    margin: 12px 0 0 0;
    padding: 14px 16px;
    width: 100%;
    border: 2px solid #d7dcde; }
    .modal input:focus {
      outline: none;
      border: 2px solid #FB565A; }
  .modal textarea {
