.modal__close {
  position: absolute;
  top: 80px;
  right: 50px;
  z-index: 12;
  box-sizing: border-box;
  width: 22px;
  height: 22px;
  border: none;
  background-color: transparent;
  opacity: 0.3;
  cursor: pointer; }
  .modal__close:hover {
    opacity: 1; }
  .modal__close:active {
    opacity: 0.2; }
  .modal__close::before {
    position: absolute;
    top: 9px;
    left: 0;
    width: 22px;
    height: 3px;
    background-color: #FB565A;
    content: "";
    transform: rotate(135deg); }
  .modal__close::after {
    position: absolute;
    top: 9px;
    left: 0;
    width: 22px;
    height: 3px;
    background-color: #FB565A;
    content: "";
    transform: rotate(-135deg); }
